/* PrevButton.css */
.prev-button {
    background-color: transparent; /* Make background transparent */
    z-index: 50;
}

/* Back button wrapper */
.prev-button-wrapper {
    font-size: 28px;
    cursor: pointer;
    /* padding: 100vh 0 0; */
    z-index: 50;
    position: absolute;
    top: 82.5%; /* Ensure the buttons are aligned with the slick list */
    right: 102%;
}

/* Circle that expands on hover */
.prev-button-wrapper::before {
    content: '';
    position: absolute;
    top: 40%; /* Center vertically */
    left: 50%; /* Center horizontally */
    width: 0;
    height: 0;
    background-color: var(--secondary-color);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(1); /* Combine translate and scale */
    transform-origin: center; /* Expand from the center */
    /* transition: width 0.1s ease-in-out, height 0.1s ease-in-out, transform 0.3s ease; Transition for size and transform */
    z-index: -1;
}

/* Circle grows on hover */
.prev-button-wrapper:hover::before {
    width: 50px;
    height: 50px;
}

/* Icon color change on hover */
.prev-button-icon {
    /* transition: color 0.1s ease; Smooth color transition for the icon */
    z-index: 2;
}

/* Change icon color on hover */
.prev-button-wrapper:hover .prev-button-icon {
    color: var(--primary-color); /* Change color on hover */
}

/* Bounce class for animation */
.prev-button-wrapper.bounce::before {
    animation: arrow_bounce 0.5s ease forwards; /* Trigger bounce animation */
}

/* Bounce keyframes */
@keyframes arrow_bounce {
    0% {
        transform: translate(-50%, -50%) scale(1); /* Start from the normal size */
    }
    50% {
        transform: translate(-50%, -50%) scale(1.3); /* Scale up for the bounce */
    }
    100% {
        transform: translate(-50%, -50%) scale(1); /* Return to normal size */
    }
}
